.coaches1 {
  position: relative;
  padding: 10px 103px 105px 103px;

  @include vp-1199 {
    padding: 10px 101px 100px 101px;
  }

  @include vp-767 {
    padding: 10px 47px 70px 47px;
  }

  h2 {
    margin: 0 auto;
    padding: 0;
    margin-bottom: 48px;
    font-family: $font-family-primery;
    font-weight: $font-weight-semibold;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 1.62px;
    width: 1160px;

    @include vp-1199 {
      font-size: 42px;
      line-height: 50px;
      letter-spacing: 1.26px;
      margin-bottom: 31px;
      text-align: center;
    }

    @include vp-767 {
      font-size: 32px;
      line-height: 38px;
      letter-spacing: 0.96px;
      margin-bottom: 42px;
    }
  }
}

.coaches1__swiper {
  width: 1160px;
  margin: 0 auto;
}

