.coaches1__trainers-slide {
  position: relative;
  width: 260px;
  height: 294px;
  margin: 0;
  padding: 0;
  min-width: 260px;

    @include vp-1199 {
      width: 268px;
    }

    @include vp-767 {
      width: 226px;
      height: 274px;
    }

    @include hover {
      .coaches1__text {
        background-color: rgba(16, 38, 84, 0.89);
      }

      h3 {
        top: 31px;
      }

      .coaches1__description {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        height: 155px;
      }

      @include vp-1199 {
        display: none;
      }
    }

    @include focus {
      .coaches1__text {
        background-color: rgba(16, 38, 84, 0.89);
      }

      h3 {
        top: 31px;

        @include vp-767 {
          top: 21px;
        }
      }

      .coaches1__description {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 155px;
        width: 100%;
      }
    }

    @include active {

      @include vp-1199 {
        .coaches1__text {
          background-color: rgba(16, 38, 84, 0.89);
        }

        h3 {
          top: 31px;

          @include vp-767 {
            top: 21px;
          }
        }

        .coaches1__description {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 155px;
        }
      }
    }
  }

