.coaches1__image-container {
  width: 260px;
  height: 294px;

  @include vp-1199 {
    width: 268px;
    height: 294px;
  }

  @include vp-767 {
    width: 226px;
    height: 274px;
  }

  img {
    background-color: $color-ghost-white;
    object-fit: cover;

    @include vp-1199 {
      width: 268px;
      height: 294px;
    }

    @include vp-767 {
      width: 226px;
      height: 274px;
    }
  }
}

.coaches1__text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
    padding: 58px 46px 58px 55px;

  @include vp-767 {
    padding: 48px 33px 48px 34px;
  }

  h3 {
    position: absolute;
    color: $color-default-white;
    margin: 0;
    padding: 0;
    left: 50%;
    bottom: 30px;
    transform: translateX(-50%);
    font-size: 18px;
    font-weight: $font-weight-bold;
    line-height: 27px;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    flex-wrap: wrap;
    height: 54px;
    align-items: end;
    justify-content: center;
    text-align: center;
  }
}

.coaches1__description {
  display: none;
  position: absolute;
  top: 81px;

  @include vp-767 {
    top: 71px;
  }

  p {
    margin: 0;
    padding: 0;
    text-align: center;
    color: $color-spindle-grey;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: auto;
  }

  ul {
    margin: 0;
    padding: 0;
    color: $color-default-white;
    list-style-type: none;

    li {
      font-size: 14px;
      line-height: 21px;
      margin: 0;
      padding: 0;
      width: 159px;
      padding-left: 16px;
      position: relative;

      &::before {
        position: absolute;
        content: "";
        width: 6px;
        height: 6px;
        background-color: $color-default-white;
        border-radius: 50%;
        top: 7px;
        left: 0;
      }
    }

      li:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
